import cx from 'clsx';
import React, { FC, MouseEvent, useState } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Label, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  ColumnsProps,
  decimalFormatter,
  DefaultSortProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditViewProps,
  ERROR,
  getSelectedItem,
  InputGroupInline,
  InputType,
  isEmpty,
  OrderType,
  percentualFormatter,
  refToFormat,
  showToast,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, ValorImposto } from '../../../../models/dtos/mfp/valorImposto';
import { ValorImpostoFaixaINSS } from '../../../../models/dtos/mfp/valorImpostoFaixaINSS';
import { ValorImpostoFaixaSalarioFamilia } from '../../../../models/dtos/mfp/valorImpostoFaixaSalarioFamilia';
import { ValorImpostoPLRFaixaIRRF } from '../../../../models/dtos/mfp/valorImpostoPLRFaixaIRRF';
import { copiarValorImpostoAnteriorAReferencia } from '../../../../services/mfp.service';
import ValorImpostoFaixaINSSEditView from './ValorImpostoFaixaINSSEditView';
import ValorImpostoFaixaIRRFEditView from './ValorImpostoFaixaIRRFEditView';
import ValorImpostoFaixaSalarioFamiliaEditView from './ValorImpostoFaixaSalarioFamiliaEditView';

interface ValorImpostoEditViewProps extends EditViewProps<ValorImposto> {}

const labelWidth: number = 340;

const ValorImpostoEditView: FC<ValorImpostoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<ValorImposto>({
    defaultValues: {
      aliquotaBaseINSSTransp: 0,
      aliquotaBaseIRRFTranspCarga: 0,
      aliquotaBaseIRRFTranspPassageiros: 0,
      aliquotaDescontoVT: 0,
      aliquotaFGTS: 0,
      aliquotaINSSAutonomo: 0,
      aliquotaINSSSocioDiretor: 0,
      aliquotaMaximaCompensacaoGps: 30,
      vrDeducaoDependenteIRRF: 0,
      vrDispensaRetencaoIRRF: 0,
      vrMinimoGps: 0,
      vrSalarioMinimo: 0,
      vrTetoINSS: 0,
      ...selectedItem,
    },
    mode: 'onBlur',
  });

  const [activeTab, setActiveTab] = useState(1);

  const teste = () => {
    if (!isEmpty(formProps.getValues().referenciaInicial)) {
      copiarValorImpostoAnteriorAReferencia(formProps.getValues().referenciaInicial, {
        errorFunction: (mensagem: string): void => {
          showToast(mensagem, ERROR);
        },
        thenFunction: (result: ValorImposto): void => {
          const ref = formProps.getValues().referenciaInicial;
          const params: AlertProps = {
            message: `Foi encontrado dados da referência ${refToFormat(result.referenciaInicial)} Deseja importa-los?`,
            onOkClick: () => {
              const valorImpostoAttrs = Object.keys(formProps.getValues());
              for (const attr of valorImpostoAttrs) {
                // @ts-ignore
                formProps.setValue(attr, result[attr]);
              }
              formProps.setValue('referenciaInicial', ref);
              formProps.triggerValidation('referenciaInicial');
            },
            type: AlertType.WARNING_YES_NO,
          };
          alertManager.emit(ACTION_EVENTS.SHOW, params);
        },
      });
    }
  };

  const toggleTab = ({ target }: MouseEvent) => {
    const { tabIndex } = target as HTMLElement;
    if (activeTab !== tabIndex) {
      setActiveTab(tabIndex);
    }
  };

  const defaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'vrRendimentoAte',
      order: OrderType.DESC,
    },
  ];

  const columnsFaixaINSS: ColumnsProps[] = [
    {
      dataField: 'vrRendimentoAte',
      formatter: decimalFormatter,
      text: 'Limite do Rendimento',
    },
    {
      dataField: 'aliquota',
      formatter: percentualFormatter,
      text: 'aliquota',
    },
  ];

  const columnsFaixaIRRF: ColumnsProps[] = [
    {
      dataField: 'vrRendimentoAte',
      formatter: decimalFormatter,
      text: 'Limite do Rendimento',
    },
    {
      dataField: 'aliquota',
      formatter: percentualFormatter,
      text: 'aliquota',
    },
    {
      dataField: 'vrDeducao',
      formatter: decimalFormatter,
      text: 'Valor Deduçào',
    },
  ];

  const columnsFaixaSalarioFamilia: ColumnsProps[] = [
    {
      dataField: 'vrRendimentoAte',
      formatter: decimalFormatter,
      text: 'Limite do Rendimento',
    },
    {
      dataField: 'vrSalarioFamilia',
      formatter: decimalFormatter,
      text: 'Valor salário Família',
    },
  ];

  const valorImpostoFaixaINSSFormProps: DetailManagerFormProps = {
    columns: columnsFaixaINSS,
    detailView: ValorImpostoFaixaINSSEditView,
    formProps,
    propName: 'faixasINSS',
    title: 'Faixa do INSS',
  };

  const valorImpostoFaixaIRFFFormProps: DetailManagerFormProps = {
    columns: columnsFaixaIRRF,
    detailView: ValorImpostoFaixaIRRFEditView,
    formProps,
    propName: 'faixasIRRF',
    title: 'Faixa do IRRF',
  };

  const ValorImpostoFaixaSalarioFamiliaFormProps: DetailManagerFormProps = {
    columns: columnsFaixaSalarioFamilia,
    detailView: ValorImpostoFaixaSalarioFamiliaEditView,
    formProps,
    propName: 'faixasSalarioFamilia',
    title: 'Faixa do Salário Família',
  };

  const limiteRendimento = 999999999999.99;

  const validateExtras = (formData: ValorImposto) => {
    const erros = [];

    const faixasINSS = formData.faixasINSS;
    const faixasIRRF = formData.faixasIRRF;
    const faixaSalarioFamilia = formData.faixasSalarioFamilia;

    if (faixasINSS) {
      faixasINSS.sort((a, b) => a.vrRendimentoAte - b.vrRendimentoAte);
      const ultimaFaixaINSS: ValorImpostoFaixaINSS = faixasINSS[faixasINSS.length - 1];

      if (Number(ultimaFaixaINSS.vrRendimentoAte) !== limiteRendimento) {
        erros.push('Valor de Limite do Rendimento na última faixa de INSS deve ser igual a 999.999.999.999,99');
      }
    } else {
      erros.push('Faixas - INSS - Deve haver pelo menos 1 faixa.');
    }

    if (faixasIRRF) {
      faixasIRRF.sort((a, b) => a.vrRendimentoAte - b.vrRendimentoAte);
      const ultimaFaixaIRRF: ValorImpostoPLRFaixaIRRF = faixasIRRF[faixasIRRF.length - 1];

      if (Number(ultimaFaixaIRRF.vrRendimentoAte) !== limiteRendimento) {
        erros.push('Valor de Limite do Rendimento na última faixa de IRRF deve ser igual a 999.999.999.999,99');
      }
    } else {
      erros.push('Faixas - IRRF - Deve haver pelo menos 1 faixa.');
    }

    if (faixaSalarioFamilia) {
      faixaSalarioFamilia.sort((a, b) => a.vrRendimentoAte - b.vrRendimentoAte);
      const ultimaFaixaSalarioFamilia: ValorImpostoFaixaSalarioFamilia = faixaSalarioFamilia[faixaSalarioFamilia.length - 1];

      if (Number(ultimaFaixaSalarioFamilia.vrRendimentoAte) !== limiteRendimento) {
        erros.push('Valor de Limite do Rendimento na última faixa de Salário Família deve ser igual a 999.999.999.999,99');
      }
    } else {
      erros.push('Faixas - Salário Família - Deve haver pelo menos 1 faixa.');
    }

    return erros;
  };

  return (
    <WindowComponent title="Manutenção de Valor e Imposto">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <InputGroupInline
              id="referenciaInicial"
              label="Referência"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: true } }}
              placeholder="Informe a Referência"
              onBlur={teste}
              type={InputType.REFERENCE}
            />
          </Col>
          <Col />
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0.01, max: 100 } }}
              id="aliquotaFGTS"
              isPrependLabel={true}
              label="Alíquota Fundo de Garantia"
              labelWidth={labelWidth}
              placeholder="Informe o valor da alíquota do fundo de garantia."
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0.01, max: 100 } }}
              id="aliquotaDescontoVT"
              isPrependLabel={true}
              label="Alíquota Desc. Vale Transporte"
              labelWidth={labelWidth}
              placeholder="Informe o valor da alíquota desc. vale transporte."
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0.01, max: limiteRendimento } }}
              id="vrSalarioMinimo"
              isPrependLabel={true}
              label="Salário Mínimo"
              labelWidth={labelWidth}
              placeholder="Informe o valor do salário Mínimo."
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0.01, max: limiteRendimento } }}
              id="vrTetoINSS"
              isPrependLabel={true}
              label="Teto do INSS"
              labelWidth={labelWidth}
              placeholder="Informe o valor do teto do INSS."
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0.01, max: limiteRendimento } }}
              id="vrDeducaoDependenteIRRF"
              isPrependLabel={true}
              label="Dedução por Dependente IRRF"
              labelWidth={labelWidth}
              placeholder="Informe o valor da dedução por dependente IRRF."
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0.01, max: limiteRendimento } }}
              id="vrDispensaRetencaoIRRF"
              isPrependLabel={true}
              label="Dispensa da Retenção IRRF"
              labelWidth={labelWidth}
              placeholder="Informe o valor da dispensa da retenção IRRF."
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0.01, max: limiteRendimento } }}
              id="aliquotaINSSSocioDiretor"
              isPrependLabel={true}
              label="Alíquota INSS Sócio/Diretor"
              labelWidth={labelWidth}
              placeholder="Informe a alíquota do INSS Sócio/Diretor."
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0.01, max: 100 } }}
              id="aliquotaINSSAutonomo"
              isPrependLabel={true}
              label="Alíquota INSS Autônomo"
              labelWidth={labelWidth}
              placeholder="Informe o valor da alíquota INSS autônomo."
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { max: limiteRendimento } }}
              id="vrMinimoGps"
              isPrependLabel={true}
              label="Valor Mínimo para GPS"
              labelWidth={labelWidth}
              placeholder="Informe o valor mínimo para GPS."
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
              id="aliquotaMaximaCompensacaoGps"
              isPrependLabel={true}
              label="Porcentual máximo para compensação (GPS)"
              placeholder="Informe o porcentual máximo para compensação (GPS)."
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Nav tabs={true}>
          <NavItem>
            <NavLink className={cx({ active: activeTab === 1 })} tabIndex={1} onClick={toggleTab}>
              Faixas - INSS
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink className={cx({ active: activeTab === 2 })} tabIndex={2} onClick={toggleTab}>
              Faixas - IRRF
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink className={cx({ active: activeTab === 3 })} tabIndex={3} onClick={toggleTab}>
              Faixas - Salário Família
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink className={cx({ active: activeTab === 4 })} tabIndex={4} onClick={toggleTab}>
              Redução das Bases
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent className="mb-1" activeTab={activeTab}>
          <TabPane tabId={1} className="mb-0 p-3">
            <Row>
              <Col>
                <DetailManagerForm
                  {...props}
                  defaultSortDirection={OrderType.DESC}
                  defaultSorted={defaultSortProps}
                  {...valorImpostoFaixaINSSFormProps}
                  keyField="rowIndex"
                  hideAddButton={true}
                  hideEditButton={true}
                  hideRemoveButton={true}
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId={2} className="mb-0 p-3">
            <Row>
              <Col>
                <DetailManagerForm
                  {...props}
                  defaultSortDirection={OrderType.DESC}
                  defaultSorted={defaultSortProps}
                  {...valorImpostoFaixaIRFFFormProps}
                  keyField="rowIndex"
                  hideAddButton={true}
                  hideEditButton={true}
                  hideRemoveButton={true}
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId={3} className="mb-0 p-3">
            <Row>
              <Col>
                <DetailManagerForm
                  {...props}
                  defaultSortDirection={OrderType.DESC}
                  defaultSorted={defaultSortProps}
                  {...ValorImpostoFaixaSalarioFamiliaFormProps}
                  keyField="rowIndex"
                  hideAddButton={true}
                  hideEditButton={true}
                  hideRemoveButton={true}
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId={4} className="mb-0 p-3">
            <Row>
              <Col>
                <div className="card mb-5">
                  <Row>
                    <Col>
                      <Label className="p-3">Alíquota sobre o rendemento bruto para compor</Label>
                      <hr className="mt-0" />
                    </Col>
                  </Row>
                  <Row>
                    <Col xs="3" />
                    <Col xs="3">Transportadores de Carga</Col>
                    <Col xs="3">Transportadores de Passageiros</Col>
                  </Row>
                  <Row>
                    <Col xs="3">
                      <Label className="p-3">Base do IRRF</Label>
                    </Col>
                    <Col xs="3">
                      <InputGroupInline
                        formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
                        id="aliquotaBaseIRRFTranspCarga"
                        isPrependLabel={false}
                        placeholder="Informe a Base do IRRF."
                        type={InputType.NUMBER}
                      />
                    </Col>
                    <Col xs="3" className="mr-3">
                      <InputGroupInline
                        formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
                        id="aliquotaBaseIRRFTranspPassageiros"
                        isPrependLabel={false}
                        placeholder="Informe a Base do IRRF."
                        type={InputType.NUMBER}
                      />
                    </Col>
                  </Row>
                  <Row>
                    <Col xs="3">
                      <Label className="p-3">Base do INSS</Label>
                    </Col>
                    <Col xs="6">
                      <InputGroupInline
                        formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
                        id="aliquotaBaseINSSTransp"
                        isPrependLabel={false}
                        placeholder="Informe a Base do INSS."
                        type={InputType.NUMBER}
                      />
                    </Col>
                  </Row>
                </div>
              </Col>
            </Row>
          </TabPane>
        </TabContent>
      </EditForm>
    </WindowComponent>
  );
};

export default ValorImpostoEditView;
