import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { ValorImpostoFaixaINSS } from '../../../../models/dtos/mfp/valorImpostoFaixaINSS';

const labelWidth: number = 180;

const ValorImpostoFaixaINSSEditView: FC<DetailEditViewFormProps<ValorImpostoFaixaINSS>> = props => {
  const limiteRendimento = 999999999999.99;
  const formProps = useForm<ValorImpostoFaixaINSS>({
    defaultValues: { vrRendimentoAte: limiteRendimento, aliquota: 0, ...props.selectedItem },
    mode: 'onBlur',
  });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { min: 0, max: limiteRendimento } }}
            id="vrRendimentoAte"
            isPrependLabel={true}
            label="Limite do Rendimento"
            labelWidth={labelWidth}
            placeholder="Informe o valor do limite do rendimento."
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
            id="aliquota"
            isPrependLabel={true}
            label="Alíquota"
            labelWidth={labelWidth}
            placeholder="Informe o valor da alíquota."
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default ValorImpostoFaixaINSSEditView;
