import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { ValorImpostoFaixaSalarioFamilia } from '../../../../models/dtos/mfp/valorImpostoFaixaSalarioFamilia';

const labelWidth: number = 170;

const ValorImpostoFaixaSalarioFamiliaEditView: FC<DetailEditViewFormProps<ValorImpostoFaixaSalarioFamilia>> = props => {
  const limiteRendimento = 999999999999.99;
  const formProps = useForm<ValorImpostoFaixaSalarioFamilia>({
    defaultValues: { vrRendimentoAte: limiteRendimento, vrSalarioFamilia: 0, ...props.selectedItem },
    mode: 'onBlur',
  });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { min: 0, max: limiteRendimento } }}
            id="vrRendimentoAte"
            isPrependLabel={true}
            label="Limite do Rendimento"
            labelWidth={labelWidth}
            placeholder="Informe o valor do limite do rendimento."
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { min: 0, max: limiteRendimento } }}
            id="vrSalarioFamilia"
            isPrependLabel={true}
            label="Valor Salário Família"
            labelWidth={labelWidth}
            placeholder="Informe o valor do salário família."
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default ValorImpostoFaixaSalarioFamiliaEditView;
